"use client";
import Protected from "@/components/Protected";
import { useEffect, useState } from "react";

export default function ManageProducts() {
    const [products, setProducts] = useState([]);

    const fetchProducts = async () => {
        const res = await fetch("http://localhost:5000/api/products");
        const data = await res.json();
        setProducts(data);
    };

    useEffect(() => {
        fetchProducts();
    }, []);

    const handleDelete = async id => {
        await fetch(`http://localhost:5000/api/products/${id}`, { method: "DELETE" });
        alert("Product deleted!");
        fetchProducts();
    };

    return (
        <Protected>
            <div className="p-10">
                <h1 className="text-2xl font-bold mb-6">Manage Products</h1>
                <div className="grid md:grid-cols-3 gap-6">
                    {products.map(p => (
                        <div key={p._id} className="border p-4 rounded shadow hover:shadow-lg">
                            <img src={p.image} alt={p.title} className="h-48 w-full object-cover rounded mb-2" />
                            <h2 className="font-bold text-lg">{p.title}</h2>
                            <p className="text-gray-600 text-sm">{p.shortDesc}</p>
                            <p className="text-green-600 font-semibold mt-1">${p.price}</p>
                            <div className="flex gap-2 mt-2">
                                <button onClick={() => handleDelete(p._id)} className="px-3 py-1 bg-red-500 text-white rounded hover:bg-red-600">
                                    Delete
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </Protected>
    );
}
